@import url('https://fonts.cdnfonts.com/css/public-pixel');

*{
    font-size: 40px;
    font-family: pixels;
    
}


body{
    background-color: #77CDFF;
}

div{
    justify-content: center;
    align-items: center;
    padding: 40px;
}

button{
    background-color: transparent;
    border-radius: 50px;
    font-family: sans-serif;
    margin: 10px;
    font-size: 30px;

    
}

input{
    border-radius: 10px;
    box-shadow: 5px 5px ;
    font-size: 30px ;
}